      :style="{ backgroundImage: `url(${image(movieInfo.backdrop_path)})` }"
    ></div>
    <div class="movie-content d-flex">
      <div style="color: white; margin-top: 18vh; margin-left: 12vh">
        <h1 class="movie-title" style="font-size: 90px">
          {{ movieInfo.title }}
        </h1>
        <div class="movie-information-wrapper d-flex align-items-center">
          <div>{{ movieInfo.release_date.split("-")[0] }}</div>
          &nbsp; <span> | </span>&nbsp;
          <div>{{ movieInfo.runtime }} 분</div>
          <!-- 분을 시간으로 쉡게 변환하는법ㅂ  -->
          &nbsp; <span class=""> | </span>&nbsp;
          <div class="d-flex">
            <div
              class="genres"
              v-for="genre in movieInfo.genres"
              :key="genre.id"
            >
              {{ genre.name }}
            </div>
          </div>
        </div>
        <div class="movie-overview">{{ movieInfo.overview }}</div>
        <!-- <div v-if="movieInfo.videos && movieInfo.videos.results">
          <iframe
            v-if="movieInfo.videos.results[0]"
            class="mt-5 movie-videos"
            :key="movieInfo.videos.results[0].key"
