import axios from "axios";
//https://api.themoviedb.org/3/movie/{{id}}/lists?api_key={{key}}
//
const request = axios.create({
  baseURL: "https://api.themoviedb.org/3/",
  params: {
    api_key: "7fb57be7831847cecee4fd1c5b1a5267",
    language: "ko-KR",
  },
});
export const movieApi = {
  nowPlaying: function () {
    return request.get("movie/now_playing");
  },
  popular: function () {
    return request.get("movie/popular");
  },
  upComing: function () {
    return request.get("movie/upcoming");
  },
  // keyy: function (id) {
  //   return request.get(`movie/${id}/videos`);
  // },
  movieInfo: (id) =>
    request.get(`movie/${id}`, {
      params: { append_to_response: "videos" },
    }),
};
