<template>
  <div>
    <div class="d-flex flex-wrap" v-if="nowPlaying">
      <div class="h4 ml-3 mt-5 mb-4 text-black">TMDB 인기 콘텐츠</div>
      <MovieLists :movieList="popular"></MovieLists>
      <div class="h4 ml-3 mt-5 mb-4 text-black">TMDB 새로 올라온 콘텐츠</div>
      <MovieLists :movieList="nowPlaying"></MovieLists>
      <div class="h4 ml-3 mt-5 mb-4 text-black">TMDB 상영/예정작</div>
      <MovieLists :movieList="upComming"></MovieLists>
    </div>
  </div>
</template>

<script>
import MovieLists from "../components/MovieLists";
import MovieText from "../components/MovieText";
import { movieApi } from "../axios/axios";
import { mapMutations } from "vuex";
export default {
  data() {
    return {
      nowPlaying: {},
      popular: {},
      upComming: {},
    };
  },
  components: {
    // MovieCard,
    MovieText,
    MovieLists,
  },
  methods: {
    ...mapMutations(["SET_LOADING"]),
    // image(img) {
    //   return `https://image.tmdb.org/t/p/w300/${img}`;
    // },
  },

  created() {
    this.SET_LOADING(true);
  },
  async mounted() {
    const { data } = await movieApi.nowPlaying();
    console.log(data.results);
    this.movieList = data.results;
    const { nowPlaying, popular, upComing } = movieApi;
    const requestArr = [nowPlaying, popular, upComing];
    const [now, pop, up] = await Promise.all(
      requestArr.map((li) => li().then((res) => res.data.results))
    );
    console.log("pop");
    console.log(pop);
    this.SET_LOADING(false);
    this.nowPlaying = now;
    this.popular = pop;
    this.upComming = up;
  },
};
</script>
